import React, {useState} from 'react';
import { Button } from '@material-ui/core';

import { useQuery } from '@apollo/react-hooks';
import Link from 'next/link' ;

import {MAIN_QUERY, MAIN_QUERY_CACHE} from './queries';
import client from './client'

import Welcome from "./Welcome"

import Myradio from './Myradio'
import Myresult from './Myresult.js'
import { UserContext } from './UserContext';

const App = () => {

  const [store, setStore] = useState([]);

  const { data } = useQuery(MAIN_QUERY,{
    variables: {
      artistId : store
    }
  });

  const ReadmyQyery = () => {
    const myCache = client.readQuery({
      query: MAIN_QUERY,
      variables: { artistId : store }
    });
    console.log(myCache)
  }

  return (
    <UserContext.Provider value={[store,setStore]}>
      <div className="App">
        <header className="App-header"> Select an artist</header>
        <Myradio />
        <Myresult data={data}/>
        <Button variant="contained" onClick={ReadmyQyery}>Read Query</Button>
      </div>
    </UserContext.Provider>
  );
}

export default App;